import { createRouter, createWebHistory, RouteRecordRaw,createWebHashHistory } from 'vue-router'
// import HomeView from '../views/HomeView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    redirect: '/newsList',
    // component: HomeView,
  },
  {
    path: '/newsList',
    name: "NewsPage",
    meta: { keepAlive: true },
    component: () => import(/* webpackChunkName: "newsList" */ "../views/newsView.vue")
  },
  {
    path: '/listsView',
    component: () => import(/* webpackChunkName: "listsView" */ "../views/listsView.vue")
  },
  {
    path: '/myView',
    component: () => import(/* webpackChunkName: "myView" */ "../views/myView.vue")
  },
  {
    path: '/newsDetails',
    name: 'newsDetails',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "newsDetails" */ '../views/newsDetails.vue')
  },
  
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
