
import { reactive, toRefs, ref, onMounted, onActivated, watch } from "vue"
import { useRoute, useRouter, onBeforeRouteUpdate } from "vue-router"

export default {
  name: "Tabbar",
  setup() {
    const Route = useRoute()
    const Router = useRouter()
    const showTabbar = ref(true)
    const tabbarIndexObj: any = {
      "/newsList": 0,
      "/listsView": 1,
      "/myView": 2,
    }
    const state = reactive({
      tabbarIndex: tabbarIndexObj[Route.path],
      includeList: ["NewsPage"],
    })

    function tabSwitch(item: any, index: any) {
      state.tabbarIndex = index
      let pathName: any = {
        0: "/newsList",
        1: "/listsView",
        2: "/myView",
      }
      Router.replace({
        path: pathName[index],
      })
    }
    watch(
      () => Router.currentRoute.value.path,
      (newValue, oldValue) => {
        // console.log("watch", newValue)
        state.tabbarIndex = tabbarIndexObj[Route.path]
        showTabbar.value = Object.keys(tabbarIndexObj).includes(Route.path)
      },
      { immediate: true }
    )

    return {
      ...toRefs(state),
      tabSwitch,
      showTabbar,
    }
  },
}
