import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "@/scss/defaule.scss"


// 按需引入ui库
import { Swiper, SwiperItem, Button, Navbar, Tabbar, TabbarItem, Tabs, TabPane, SearchBar, Toast, Category, CategoryPane, Empty, InfiniteLoading, Sku, OverLay, Popup, InputNumber, Price, Cell, BackTop, Input, Icon } from "@nutui/nutui"
import "@nutui/nutui/dist/style.css";

createApp(App)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Button)
  .use(Tabs)
  .use(TabPane)
  .use(Navbar)
  .use(SearchBar)
  .use(Toast)
  .use(Category)
  .use(CategoryPane)
  .use(Swiper)
  .use(SwiperItem)
  .use(Empty)
  .use(InfiniteLoading)
  .use(Sku)
  .use(Popup)
  .use(Input)
  .use(InputNumber)
  .use(Price)
  .use(Cell)
  .use(BackTop)
  .use(OverLay)
  .use(Icon)
  .use(store)
  .use(router)
  .mount('#app')
